table{
    width: 100%;

    text-align: center;
    position: relative;
    margin: 0 0 40px 0;
 
}
    
    tr{
      position: relative;
      width: 100%;
      
     
      }
      
     
    
    .feat:hover{
        cursor: pointer;
    }
    .desc {
      display: none;
      background: #e1e1e1;
     
    }
    
    th{
      background: blueviolet;
      width: 15%;
      color: #fff;
      border-left: 1px solid rgba(255, 255, 255, 0.2);
    } 
      .icons{
        top: 0px;
        left: 4px;
        position: relative;
        color: rgba(255,255,255,0.6);
      }
      
      ul{
        width: 100%;
        background: #ce1576;
        display: none;
      
      }
    td{
      padding: 12px 6px;
      font-size: 0.9em;
      vertical-align: middle;    
      
    }
    #dis
    {
        text-align:left;
    }
  
    .button {
        
        font-size: 16px;
        text-align: center;
        cursor: pointer;
        outline: none;
        color: #fff;
        background-color: lightseagreen;
        border: none;
        border-radius: 5px;
        box-shadow: 0 5px #999;
      }
      
      .button:hover {background-color: #3e8e41}
      
      .button:active {
        background-color: blue;
        box-shadow: 0 5px #666;
        transform: translateY(4px);
      }
  
      .table{
        overflow-y: scroll; 
        height:420px;
      }
      .fun{
        position: fixed;
        
      }